.a {
    height: 100vh;
    display: flex;
    align-items: center;
}

.a-left {
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.a-right {
    flex:1;
}

.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: darkslategray;
}

.a-card {
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.a-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.a-desc {
    margin-right: 10%;
}